.detailsContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 2rem;
}

.detailsContainer img {
    border-radius: 10px;
    max-width: 100%;
    height: auto;
}

.detailsContainer h2,
.detailsContainer p {
    margin: 0.5rem 0;
    text-align: center;
}

.detailsContainer button {
    background-color: #333;
    color: white;
    border: none;
    padding: 0.5rem 1rem;
    border-radius: 5px;
    cursor: pointer;
    margin-bottom: 2rem;
}

.detailsContainer button:hover {
    background-color: #555;
}